
import tabGroup from '@/components/group/tab'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {getAddressList } from '@/api/address'

export default {
components: {
tabGroup: tabGroup,
Group,
Toast
},
data () {
return {
title: '收货地址',
adress: true,
adresslist: [],
skipRouter: ''
}
},
props: {},
computed: {},
mounted () {
this.getaddress()
},
methods: {
newchange () {
this.$router.push('./newAddress')
},
tochangeadress (item) {

this.$router.push({
path: './editAddress',
query: { id: item.addressId }
})
},
getaddress () {

getAddressList({
userId:this.$store.getters.userInfo.userId,//this.$store.getters.userInfo.userId,
pageNum: 1, pageSize: 100
}).then(response => {

console.log(response)
this.adresslist = response.data




})

},
selectAddress (item) {
if (this.skipRouter !== 'HelloWorld') {
this.$store.commit('setSelectedAddress', item);
this.$vux.toast.show({
text:'切换成功'
})
this.$router.go(-1)
}
}
},
beforeRouteEnter (to, from, next) {
next(vm => {          //  这里的vm指的就是vue实例，可以用来当做this使用
vm.skipRouter = from.name
})
}
}



